    }
}

@media ( max-width: 380px ){
    .header{
        justify-content: center;
    }
}

@media (min-width:425px) and (max-width: 767px){
    .MuiBottomNavigationAction-root{
        padding: 8px 33px 8px;
        min-width: 64px !important;
    }
  
    .title-Header{
        display: block;
    }
  }
.loading{
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.slider-movies {
  height: 53vh;
  width: 100%;
}

.slider-movies__movie {
  height: 55vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.slider-movies__movie-info {
  display: flex;
  align-items: flex-end;
  height: 100%;
  padding-bottom: 50px;
}

.div-info {
  background-color: #8d8c8ccf;
  margin: 0 auto;
  text-align: center;
  width: 60%;
  padding-bottom: 13px;
  color: black;
}

.boton {
  padding-bottom: 5px;
  color: rgb(212, 202, 202);
  border-radius: 70px;
}

.titulo {
  color: rgb(39, 39, 39);
}

.parrafo {
  padding-bottom: 10px;
  color: rgb(36, 36, 36);
}

@media (max-width: 1000px) {
  .slider-movies__movie-info div {
    width: 90%;
  }
}

@media (max-width: 500px) {
  .slider-movies__movie-info div {
    font-size: 10px;
    width: 95%;
    padding: 5px;
  }
  .boton {
    font-size: 10px;
    height: 20px;
    padding: 1px 5px;
  }
}

.movie-list {
  width: 70%;
  margin: 0 auto !important;
  margin-top: 60px !important;
  border-color: rgb(65, 120, 122) !important;
}

.ant-list-header {
  background-color: rgb(45 41 51);
}

.ant-list-header h2 {
  text-align: center;
  margin: 0;
  color: white;
}

.ant-list-items {
  height: 60vh;
  overflow: scroll;
  background-color: rgb(176, 188, 189);
}

.ant-list-item-meta {
  align-items: center;
}

.ant.list.item-meta-title {
  margin: 0;
}

.boton-info {
  background-color: rgb(101, 97, 109);
}

.ant-row {
  padding-bottom: 90px;
}

@media ( max-width: 767px ){
  .ant-col{
    width: 100%;
    padding: 0;
  }
}

@media ( max-width: 767px ){
  .ant-row{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .ant-col-12 {
    max-width: 100% !important;
  }
}




.modal-video{
    width: 100% !important;
    height: 100% !important;
    padding: 0 !important;
}

.ant-modal-content{
    height: 100%;
    background-color: black !important;
}

.ant-modal-close{
    color: white;
}

.ant-modal-close span{
    font-size: 1em;
    margin-right: 20%;
    margin-top: 20%;
}

.ant-modal-body{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

  .ant-modal-body div{
    width: 90% !important;
    height: 90% !important;
} 
.movie {
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.movie__dark {
  position: absolute;
  top: 82;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: black;
  opacity: 0.6;
}

.movie__poster {
  height: 60vh;
  margin-left: 200px;
  margin-right: -80px;
}

.movie__poster div {
  height: 100%;
  width: 300px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  margin: 0;
}

.row-movie {
  display: flex !important;
  height: 100% !important;
  align-items: center !important;
  width: 100%;
}

.ant-col-9 {
  max-width: 50%;
}

.movie__info {
  color: white;
  height: 75%;
}

.movie__info-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70%;
}

.titulo-info-header {
  color: white;
  margin: 0;
  font-size: 2.5em;
}

.span-info-header {
  font-size: 20px;
  margin-left: 12px;
  opacity: 0.7;
}

.boton-trailer {
  height: 100%;
  background-color: rgb(216, 215, 215);
  color: black;
  border: 0;
  font-size: 16px;
  border-radius: 40px;
}

.boton-trailer:hover {
  color: rgb(34, 23, 66);
  outline: none;
  border: none;
  cursor: pointer;
}

.letras-info {
  color: rgb(170, 170, 170);
  font-weight: bold;
  font-size: 1em;
}

.movie__info-content {
  margin-top: 50px;
}

.parrafo-info {
  color: white;
}

@media (max-width: 480px) {
  .movie__poster div {
    margin-left: -40px;
  }

  .boton-trailer {
    font-size: 12px;
    padding: 3px 3px 3px;
  }

  .parrafo-info {
    font-size: 10px;
  }

  .movie__info-content {
    margin-left: -60px;
    width: 140%;
  }
  .movie__poster div {
    margin-left: -150px;
  }

  .titulo-info-header {
    font-size: 15px;
    padding-right: 5px;
    padding-top: 15px;
  }
  .span-info-header {
    font-size: 12px;
    padding-right: 5px;
    padding-top: 10px;
  }

  .letras-info {
    font-size: 12px;
  }
}

@media (max-width: 767px) {
  .movie,
  .movie__dark {
    height: 160vh;
  }

  .movie__poster {
    margin-right: 50px;
  }

  .movie__poster div {
    width: 250px;
  }

  .movie__info-content {
    margin-left: -60px;
    width: 150%;
  }
  .movie__poster div {
    margin-left: -160px;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .movie,
  .movie__dark {
    height: 150vh;
    width: 100%;
  }

  .movie__poster div {
    width: 200px;
  }

  .movie__info-content {
    margin-left: -60px;
    width: 100%;
  }
  .movie__poster div {
    margin-left: -130px;
  }

  .parrafo-info {
    font-size: 15px;
  }

  .letras-info {
    font-size: 23px;
  }
}

